import { ViewContainerRef, Component, ElementRef, OnInit, ViewChild, Input, Output, EventEmitter } from '@angular/core';

declare const Microsoft: any;

@Component({
  selector: 'my-map',
  template: `
  <div id='myMap' style='width: 100vw; height: 100vh;'></div>
  `,
  styles: []
})
export class MymaplibComponent implements OnInit {
  @Input() ticketList: any = []
  @Output() ticketClick: EventEmitter<any> = new EventEmitter<any>();

  title = 'myMapApp';
  base64Image = 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABkAAAAcBAMAAABmCgnjAAAAFVBMVEVHcEz///8aGhpsuy2s4oDRuPMaADiElDMGAAAAAXRSTlMAQObYZgAAAFNJREFUeF7FkLENgDAQA52GGiIxwD8bPBsgBmD/aXCs6JMNcpWtk1wYx8SO6gO2N3kWtDuJBc39UtYTPEc69BIpTVqLgjJOpS7tQ1I5NyiGia2rHyT7OTg7xhBoAAAAAElFTkSuQmCC';
  @ViewChild('mapElement') someInput: ElementRef;
  myMap = document.querySelector('#myMap');

  ngOnInit() {
    if (document.readyState != 'complete') {
      document.onreadystatechange = () => {
        if (document.readyState === 'complete') {
          this.loadMapView();
        } else {
          this.ngOnInit();
        }
      }
    } else {
      if (document.readyState === 'complete') {
        this.loadMapView();
      }
    }


  }

  loadMapView() {
    var that = this;
    var map = new Microsoft.Maps.Map(document.getElementById('myMap'), {
      /* No need to set credentials if already passed in URL */
      center: new Microsoft.Maps.Location(40.3969926, -74.1408029),
      credentials: 'AnxpS-32kYvBzjQ5pbZcnDz17oKBa1Bq2HRwHANoNpHs3Z25NDvqbhcqJZyDoYMj',
      zoom: 12,
      liteMode: true,
      enableClickableLogo: false,
      showLogo: false,
      showTermsLink: false,
      showMapTypeSelector: false,
      showTrafficButton: true,
      enableSearchLogo: false,
      showCopyright: false
    });
    var infobox = new Microsoft.Maps.Infobox(map.getCenter(), {
      visible: false
    });
    infobox.setMap(map);    

    this.ticketList.forEach(element => {
      var pushpin = new Microsoft.Maps.Pushpin(new Microsoft.Maps.Location(element.lattitude, element.longititude), { icon: this.base64Image, text: element.severity });
      pushpin.metadata = element;
      map.entities.push(pushpin);

      Microsoft.Maps.Events.addHandler(pushpin, 'click', (args) => {
        
        infobox.setOptions({
          showPointer: true,
          location: args.target.getLocation(),
          visible: true,
          showCloseButton: true,
          offset: new Microsoft.Maps.Point(0, 20),
          title: 'Ticket No: ' + args.target.metadata.ticketNo, 
          description: args.target.metadata.description, 
          //htmlContent: '<div class = "infy" style= "background-color: white;border: 1px solid lightgray; width:560px;height:50px;"></div>'
        });
        console.log('ticket pin clicked');
        pinClicked(args.target.metadata)
    });

  });
    //var pushpin = new Microsoft.Maps.Pushpin(map.getCenter(), { icon: this.base64Image, text: 'A', title: 'Title', subTitle: 'Subtitle' });

    // var pushpin = new Microsoft.Maps.Pushpin(map.getCenter(), { icon: this.base64Image, anchor: new Microsoft.Maps.Point(12, 28) });
    function pinClicked(parms){
      console.log('emit',that);
      that.ticketClick.emit(parms);
    }
  }

  

}
